import React, { useEffect, useState } from "react";
import{Link, useParams} from "react-router-dom"
let IMGPATH = "https://image.tmdb.org/t/p/w1280";
const IDAPI = "https://api.themoviedb.org/3/movie/{movie_id}?api_key=04c35731a5ee918f014970082a0088b1&language=en-US"

const MovieFullPage = ()=> {
const {id} = useParams()
const [movie, setMovie] = useState()

const getMovies = (IDAPI) => {
  fetch(IDAPI)
    .then((res) => res.json())
    .then((data) => {
      console.log(data)
     
   setMovie(data)
    });
};

useEffect(() => {
 const url = IDAPI.replace("{movie_id}", id)
 getMovies(url)
}, [id])

if(!movie) return <div className="loading">Loading Movie..</div>

const {  title,
  poster_path,
  overview,
  vote_average,
  backdrop_path,
  release_date
 } = movie
  return (
    <div className="single-movie">
 <div className="movie-header">
        <img src={IMGPATH + (poster_path || backdrop_path) } alt={title}  loading="lazy"/>
      </div>
      <div className="single-info">
       <h2>{title}</h2>
      
       <p>{overview}</p>
       <h3>{release_date}</h3>
       <Link to='/' className='btn'>
          All movies
        </Link>
      </div>
     
    </div>

  );
};

export default MovieFullPage;

