import React from "react";
import {Link}  from "react-router-dom"
let IMGPATH = "https://image.tmdb.org/t/p/w1280";

const Movie = ({
  title,
  poster_path,
  overview,
  vote_average,
  backdrop_path,
  id
}) => {
  if (!poster_path || !backdrop_path) return <></>;

  return (
    <Link  to={`/movie/${id}`}className="movie">
      <div className="movie-header">
        <img src={IMGPATH + (poster_path || backdrop_path) } alt={title}  loading="lazy"/>
      </div>
      <div className="movie-info">
        <h3>{title}</h3>
      </div>
    </Link>
  );
};

export default Movie;
